.forecast-preview {
    height: 100%;
    box-shadow: 0px 1px 4px rgba(0, 0, 0, 0.16);
    padding: 1rem;
    border-radius: 3px;
    img {
        width: 150px;
    }
    .weather-description {
        text-align: center;
        padding: 5px 8px;
    }
    &.dark {
        box-shadow: 0px 1px 4px rgba(250, 250, 250, 0.16);
        button{
            background: linear-gradient($clr6, $clr4);
            color: $clr3;
            border: 1px solid $clr6;
            &:hover{
                background: linear-gradient($clr3, $clr2); 
                color: $clr4;
            }
        }
    }

    button {
        display: inline-block;
        min-width: 100%;
        border-radius: 3px;
        color: $clr4;
        padding: 5px;
        background: linear-gradient($clr3, $clr2);
        opacity: 0.8;
        border: 1px solid $clr3;

        &:hover {
            background: linear-gradient($clr6, $clr4);
            color: $clr3;

            opacity: 1;
        }
    }

    @include from-normal-layout {
        button {
            max-width: 80%;
        }
    }
}
